import React, { Component } from 'react';
import './index.css';
// import FontAwesomeIcon from '@fortawesome/react-fontawesome'
import FontAwesomeIcon from 'react-fontawesome';
import GA from '../../lib/utils/GA';
import me from '../../lib/constants/me';

const brandColors = {
  linkedIn: '#0077B5',
  facebook: '#3b5998',
};

const myLinkedIn = 'https://www.linkedin.com/in/chinloongtan';

class Footer extends Component {
  state = {
    isLinkedInActive: false,
    isFacebookActive: false,
  };

  render() {
    return (
      <footer className="">
        <div className="container">
          <h4 className="">
            Contact me via:
          </h4>

          <a
            href={myLinkedIn}
            target="_blank"
            rel="noopener noreferrer"
            onClick={() => GA.trackLinkEvent(myLinkedIn)}
            style={{color: 'white'}}
          >
            <FontAwesomeIcon
              name={'linkedin-square'}
              size="2x"
              style={{color: 'white', marginRight: 15, marginTop: 15, marginBottom: 15,}}
            />
            {myLinkedIn}
          </a>

          <div className="" style={{ marginBottom: 0, color: 'white' }}>
            <a href={me.mailto} onClick={() => GA.trackLinkEvent('contactEmail')} style={{ color: 'white' }}>
              <FontAwesomeIcon
                name="envelope-open-o"
                size="2x"
                style={{color: 'white', marginRight: 10, marginTop: 15, marginBottom: 15,}}
              />
              {me.email}
            </a>
          </div>
        </div>

      </footer>
    );
  }
}

export default Footer;



